.projectsBlock{
    min-height: 100vh;
    background-color: #c2bed9;
    padding: 50px 0;
}
.projectsContainer{
    align-items:center;
    flex-direction: column;
    justify-content: flex-start;
}
.title{
    background-color: #fab8b8;
}
.projects{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}